import React from 'react';
import { Radar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from 'chart.js';
import { useTranslation } from 'react-i18next';

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

interface RadarChartProps {
  stats: { [key: string]: number };
}

const RadarChart: React.FC<RadarChartProps> = ({ stats }) => {
  const { t } = useTranslation();

  const labels = [
    t('popularity'),
    t('individualCount'),
    t('cuteness'),
    t('coolness'),
    t('visibility'),
  ];

  const data = {
    labels: labels,
    datasets: [
      {
        label: t('stats'),
        data: [
          stats.popularity || 0,
          stats.individualCount || 0,
          stats.cuteness || 0,
          stats.coolness || 0,
          stats.visibility || 0,
        ],
        backgroundColor: 'rgba(0, 191, 255, 0.4)', // 
        borderColor: 'rgba(0, 191, 255, 1)',     // 
        borderWidth: 3, // 
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      r: {
        angleLines: {
          display: false,
        },
        suggestedMin: 0,
        suggestedMax: 10, // 10
        ticks: {
          display: true, // 
          stepSize: 2, // 2
          font: {
            size: 10, // 
          },
        },
        pointLabels: {
          font: {
            size: 20, //  (40)
            family: 'Noto Sans JP', // 
            weight: 700, //  ()
          },
        },
      },
    },
    plugins: {
      legend: {
        display: false, // 
      },
    },
    elements: {
      line: {
        borderWidth: 2,
      },
      point: {
        radius: 3,
        backgroundColor: 'rgba(0, 191, 255, 1)', // 
      },
    },
  };

  return (
    <div className="radar-chart-container"> {/* CSS */}
      <Radar data={data} options={options} />
    </div>
  );
};

export default RadarChart;